@import "fonts"; // Шрифты
@import "reset"; // Сброс стилей
@import "setting"; // Общие настройки
@import "vars"; // Переменные
@import "mixins"; // Миксины

// Компоненты
@import "components/footer";
@import "components/header";

body {
  padding-top: 40px;
}

// Стили
.container-with-aside {
  display: flex;
  aside {
    width: 40%;
    margin-right: 30px;
    min-width: 205px;
  }
}

.sec-products {
  position: relative;
  &:hover {
    z-index: 100;
  }

  &__title {
    margin-bottom: 20px;
  }
  &__top {
    &_margin {
      margin-bottom: 60px;
    }
  }
}

.products-el {
  padding-top: 18px;
  background: #ffffff;
  border: 1px solid #dddddd;
  // border-radius: 3px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  transition: 0.3s;

  .products-el__actions {
    opacity: 0;
    transition: 0.3s;
  }
  &:hover {
    .products-el__actions {
      opacity: 1;
    }
  }

  &__top {
    position: relative;
    display: block;
    height: 200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
  &__topWrap {
    padding: 0 20px;
    margin-bottom: 19px;
    width: 100%;
  }
  &__body {
    width: 100%;
    background-color: #fff;
    position: relative;
    z-index: 20;
    padding: 18px 20px;
    padding-top: 0 !important;
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  &__sale {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
  }
  &__hover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    z-index: 10;
    div {
      width: 100%;
      height: 100%;
      cursor: pointer;
    }
  }
  &__img {
    z-index: 5;
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
      &:not(:first-child) {
        display: none;
      }
    }
  }
  &__actions {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 20;
  }
  &__dots {
    margin-bottom: 10px;
    width: 100%;
  }
  &__rating {
    margin-bottom: 11px;
    width: 100%;
  }
  &__title {
    font-family: "Montserrat";
    font-weight: 700;
    font-size: 16px;
    line-height: 25px;
    color: #333333;
    line-height: 1.55;
    letter-spacing: -0.2px;
    margin-bottom: 12px;
    display: block;
    width: 100%;
  }
  &__info {
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    color: #333333;
    width: 100%;
    margin-bottom: 29px;
    line-height: 1.3;
    p {
      margin-bottom: 2px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__art {
    margin-bottom: 12px;
  }
  &__cost {
    margin-top: auto;
    width: 100%;
    font-weight: 700;
    font-size: 16px;
    color: #333333;
  }
  &__dropdown {
    position: absolute;
    top: 100%;
    left: -1px;
    right: -1px;
    width: auto;
    background-color: #fff;
    border: 1px solid #ddd;
    border-top: 0;
    border-bottom: 0;
    text-align: center;
    padding-top: 21px;
    opacity: 0;
    visibility: hidden;
    transition: 0.1s;
  }
  &__calc {
    margin-bottom: 20px;
  }
  &__btn {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    margin-top: 20px;
    height: 48px;
  }
  &_2 {
    border-radius: 0;
    .products-el__top {
      height: 230px;
    }
    .products-el__topWrap {
      margin-bottom: 14px;
    }
    .products-el__info {
      margin-bottom: 40px;
    }
  }
  &_3 {
    border-radius: 0;
    .products-el__top {
      height: 240px;
    }
    .products-el__topWrap {
      margin-bottom: 24px;
    }
    .products-el__info {
      margin-bottom: 40px;
    }
    .products-el__body {
      min-height: 240px;
    }
    .products-el__info {
      p {
        &:nth-child(n + 3) {
          display: none;
        }
      }
    }
    &:hover {
      .products-el__info {
        p {
          display: block;
        }
      }
    }
  }
  &_cover {
    .products-el__img-img {
      object-fit: cover;
    }
  }
  &:hover {
    box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
    z-index: 100;
    .products-el__dropdown {
      padding: 10px;
      opacity: 1;
      visibility: visible;
      box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
    }
  }
  &_hover {
    .products-el__actions {
      opacity: 0;
      transition: 0.3s;
    }
    &:hover {
      .products-el__actions {
        opacity: 1;
      }
    }
  }
}

.block-actions {
  display: flex;
  &__el {
    margin-right: 5px;
    &:last-child {
      margin-right: 0;
    }
  }
}

.actions-pr-el {
  width: 36px;
  height: 36px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 0;
  transition: 0.3s;
  margin-bottom: 5px;
  .stroke,
  .fill {
    transition: 0.3s;
  }
  &:hover,
  &.active {
    background: #b54817;
    .stroke {
      stroke: #fff;
    }
    .fill {
      fill: #fff;
    }
  }
}
.products-el__info p {
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
}

.block-products {
  display: flex;
  flex-wrap: wrap;
  position: relative;

  &__el {
    width: 25%;

    .products-el {
      height: 100%;
    }

    @media (max-width: 992px) {
      width: 33.33%;
    }
    @media (max-width: 768px) {
      width: 50%;
    }
    @media (max-width: 576px) {
      width: 100%;
    }
  }
  &:hover {
    z-index: 100;
  }
}

.block-dots {
  display: flex;
  &__el {
    height: 2px;
    width: 100%;
    background-color: #dddddd;
    margin-right: 2px;
    &.active {
      background-color: #b54817;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

.product-el-hint {
  display: flex;
  position: relative;
  z-index: 5;
  .hint-sale,
  .hint-new {
    font-weight: 400;
    font-size: 15px;
    line-height: 25px;
    background: #fafafa;
    border: 1px solid #ececec;
    display: block;
    padding: 5px 10px;
    margin-right: 10px;
  }
}

.calc-product-el {
  display: flex;
  align-items: center;
  &__text {
    font-weight: 400;
    font-size: 15px;
    color: #333333;
    margin-left: 10px;
  }
}

.block-calc-product {
  display: inline-block;
  width: 172px;
  &__el {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.calc-product-act {
  width: 140px;
  height: 48px;
  background: #fafafa;
  border: 1px solid #999999;
  display: flex;
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 40px;
    user-select: none;
    svg {
      path {
        stroke: #999999;
      }
    }
  }
  &__inp {
    display: block;
    flex: 1;
    border: 0;
    padding: 0;
    width: 50px;
    text-align: center;
    background: transparent;
    font-family: "Montserrat";
    font-weight: 400;
    font-size: 15px;
    color: #999999;
    text-align: center;
  }
}

.text-info-gray_margin {
  margin-bottom: 8px;
}
.text-info-gray {
  color: #333;
}
.text-info,
.text-info-gray {
  font-weight: 400;
  font-size: 15px;
}

.text-cost {
  font-size: 18px;
}
.cost-def,
.text-cost {
  font-weight: 700;
  color: #333;
}

.products-el__btn {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  margin-top: 20px;
  height: 48px;
  font-size: 15px !important;
  font-weight: 400 !important;
}
.btn-def {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  font-weight: 500;
  font-size: 10px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  color: #fff;
  background: #b54817;
  padding: 13px 20px;
  border: 1px solid #9f3f13;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.products-el__btn {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  margin-top: 20px;
  height: 48px;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input,
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  margin: 0;
}

.categories-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 25px;
  color: #333333;
  margin-bottom: 25px;
}

.categories__type-title {
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 0.008em;
  color: #333333;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  svg {
    transition: 0.2s linear;
  }
  margin-bottom: 15px;
}

.categories__type-title.active svg {
  transform: rotate(180deg);
}

.categories__type {
  &-items {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 25px;
    &--hide {
      display: none !important;
    }
    li {
      padding: 5px 10px;
      background: #fafafa;
      border: 1px solid #ececec;
      border-radius: 3px;
      margin-right: 10px;
      margin-bottom: 10px;
      transition: opacity 0.2s linear;
      cursor: pointer;
      &:hover {
        opacity: 0.5;
      }
      a {
        font-weight: 400;
        font-size: 15px;
        line-height: 25px;
        color: #333333;
      }
    }
  }
}

.container-with-aside {
  @media (max-width: 562px) {
    flex-direction: column;
    aside {
      width: 100%;
      margin-bottom: 30px;
    }
  }
  .block-products {
    &__el {
      @media (max-width: 1200px) {
        width: 33.33%;
      }

      @media (max-width: 991px) {
        width: 50%;
      }

      @media (max-width: 768px) {
        width: 100%;
      }
    }
  }
}

.filter {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  @media (max-width: 576px) {
    flex-direction: column;
  }
  .found {
    display: flex;
    align-items: center;
    @media (max-width: 576px) {
      flex-direction: column;
    }
    &-number {
      font-weight: 700;
      font-size: 13px;
      line-height: 20px;
      padding: 5px 10px;
      background: #fafafa;
      border: 1px solid #ececec;
      margin: 0px 8px;
      @media (max-width: 576px) {
        flex-direction: column;
        margin: 8px;
      }
    }
  }

  .switcher-wrapper {
    margin-right: 8px;

    input {
      display: none;
    }
    .switcher-item {
      width: 50px;
      height: 30px;
      padding: 5px;
      background: #dddddd;
      border-radius: 100px;
      display: block;
      cursor: pointer;
      div {
        width: 20px;
        height: 20px;
        background: #9f3f13;
        border-radius: 100px;
        transition: 0.1s linear;
      }
    }

    input:checked + .switcher-item {
      div {
        transform: translateX(20px);
      }
    }
  }

  .products-count {
    font-weight: 700;
    font-size: 13px;
    border-bottom: 1px solid #ececec;
    padding: 2px 5px;
    @media (max-width: 576px) {
      margin-top: 8px;
    }
  }
}

.products-el__dropdown.more-info p {
  text-align: left;
  padding-left: 10px;
}

.pagination {
  width: max-content;
  margin: 0 auto;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;

  .show-more {
    font-size: 15px;
    line-height: 25px;
    color: #000000;
    background: #fafafa;
    border: 1px solid #ececec;
    padding: 10px 100px;
    cursor: pointer;
    transition: 0.2s linear;
    margin-bottom: 20px;
    &:hover {
      opacity: 0.7;
    }
  }

  &-links {
    display: flex;
    align-items: center;
    border-collapse: collapse;
    li {
      padding: 5px 15px;
      min-height: 36px;
      cursor: pointer;

      border: 1px solid #ececec;
      &:first-child {
        border-radius: 3px 0px 0px 3px;
      }
      &:last-child {
        border-radius: 0px 3px 3px 0px;
      }
      a {
        color: #b54817;
        transition: 0.2s linear;
        font-weight: 400;
        font-size: 15px;
        line-height: 25px;
      }
      transition: 0.2s linear;
      &:hover {
        background: #b54817;
        svg {
          path {
            fill: #fff;
          }
        }
        a {
          color: #fff;
        }
      }
    }
    li.active {
      background: #b54817;
      a {
        color: #fff;
      }
    }
  }
}

.icons-with-text {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 970px;
    margin: 0 auto;
  }
  &__item {
    max-width: 240px;
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .text {
      padding-top: 20px;
      font-weight: 400;
      font-size: 15px;
      line-height: 25px;
      text-align: center;
      color: #000000;
    }
  }
}
